import React from 'react';
import MenuImage01 from '../assets/img/header/finance.png';
import MenuImage02 from '../assets/img/header/car_exchange.png';
import MenuImage03 from '../assets/img/header/refinance.png';
import { CardButton } from '../components/Card';
import { LazyLoadImage } from 'react-lazy-load-image-component';


export default function Menu() {
	var Menu01 = 
		<CardButton
			to='/usedcarfinance'
			className='card__menu'
			image={
				<LazyLoadImage src={MenuImage01} alt='สินเชื่อจัดไฟแนนซ์รถมือสอง รถบ้าน' style={{height: '120%', marginBottom: 45}}/>
			}
			text={
				<h2 className='text__30 dark__blue' style={{margin: 0, display: 'block'}}>จัดไฟแนนซ์รถมือสอง<br/>รถบ้าน</h2>
			}
		/>
	
	var Menu02 = 
		<CardButton
			to='/carforcash'
			className='card__menu'
			image={
				<LazyLoadImage src={MenuImage02} alt='รถแลกเงิน จำนำทะเบียนรถ' style={{height: '120%', marginBottom: 65}}/>
			}
			text={
				<h2 className='text__30 dark__blue' style={{margin: 0, display: 'block'}}>รถแลกเงิน<br/>จำนำทะเบียนรถ</h2>
			}
		/>

		var Menu03 = 
		<CardButton
			to='/carrefinance'
			className='card__menu'
			image={
				<LazyLoadImage src={MenuImage03} alt='รีไฟแนนซ์รถ' style={{height: '120%', marginBottom: 35}}/>
			}
			text={
				<h2 className='text__30 dark__blue' style={{margin: 0, display: 'block'}}>รีไฟแนนซ์รถ</h2>
			}
		/>

	return (
		<div className='content' style={{paddingBottom: 50}}>
			<div className='content__inline'>
				{Menu01}
				{Menu02}
				{Menu03}
			</div>
		</div>
	)
}