import React, { useState } from 'react';

import { Card } from '../components/Card';
import Swiper from 'react-id-swiper';
import MediaQuery from 'react-responsive';
import LeftArrow from '../assets/img/left_arrow.svg';
import RightArrow from '../assets/img/right_arrow.svg';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import update from 'immutability-helper';

export default function GetStarted(props) {
    let GetStarted = []
    let [swiper, updateSwiper] = useState(null);
    let [page, setPage] = useState(0);
    let [isBeginning, setIsBeginning] = useState(true);
    let [isEnd, setIsEnd] = useState(false);
    let [isShow, setIsShow] = useState(new Array(props.getStartedData.length).fill(false));

    for (let i = 0; i < props.getStartedData.length; i++) {
        GetStarted.push(
            <Card
                key={i}
                className='card__get-started'
                image={<LazyLoadImage src={props.getStartedData[i].image} alt={props.getStartedData[i].imageAlt} className='get-started__card__image' />}
                text={
                    <div>
                        {/* mobile */}
                        <MediaQuery maxWidth={768}>
                            <div className='text__20 text__thai text__bold' style={{ margin: 0 }}>
                                {props.getStartedData[i].headerTextMobile}
                            </div>
                        </MediaQuery>

                        <MediaQuery maxWidth={768}>
                            <div className='small__text text__thai card__none__interaction'>
                                <p style={{ visibility: !isShow[i] ? 'visible' : 'hidden', color: '#445DFF', textDecoration: 'underline', fontWeight: 'bold', fontSize: 14 }} onClick={function () { setIsShow(update(isShow, { [i]: { $set: true } })); }}>รายละเอียดเพิ่มเติม</p>
                            </div>
                        </MediaQuery>

                        {/* PC */}
                        <MediaQuery minWidth={769}>
                            <div className='larger__text' style={{ margin: 0 }}>
                                {props.getStartedData[i].headerText}
                            </div>
                        </MediaQuery>

                        <MediaQuery minWidth={769}>
                            <div className='get-started__text text__thai' style={{ marginTop: 20, marginLeft: 30 }}>
                                {props.getStartedData[i].bodyText}
                            </div>
                        </MediaQuery>

                    </div>
                }
                isInteraction={true}
                isShowInteraction={isShow[i]}
                interaction={
                    <div className='text__18 text__thai'>
                        {props.getStartedData[i].bodyTextMobile || props.getStartedData[i].bodyText}
                        <div className='small__text text__thai'>
                            <p style={{ color: '#445DFF', textDecoration: 'underline', fontWeight: 'bold', fontSize: 14, marginTop: 15 }} onClick={function () { setIsShow(update(isShow, { [i]: { $set: false } })); }}>ปิดรายละเอียด</p>
                        </div>
                    </div>
                }
            />
        )
    }

    if (swiper !== null) {
        swiper.on('slideChange', function () {
            setPage(swiper.activeIndex)
            setIsBeginning(swiper.isBeginning)
            setIsEnd(swiper.isEnd)
        });
    }

    const goNext = () => {
        if (swiper !== null) {
            swiper.slideNext();
        }
    };

    const goPrev = () => {
        if (swiper !== null) {
            swiper.slidePrev();
        }
    };

    const params = {
        pagination: {
            el: '.carousel__pagination',
            type: 'bullets',
            clickable: true,
            renderBullet: function (index, className) {
                return `<p class="${className}">${index + 1}<br> </p>`;
            },
            bulletClass: "carousel__pagination__button--text",
            bulletActiveClass: "carousel__pagination__button--text--active"
        },
    }

    return (
        <div className='content' style={{ paddingBottom: 60 }}>
            <h3 className='larger__text header__text dark__blue'>เริ่มต้นอย่างไร?</h3>

            <div className='carousel__container'>
                <Swiper containerClass="carousel__reveser" {...params} getSwiper={updateSwiper}>
                    {GetStarted}
                </Swiper>

                <MediaQuery minWidth={769}>
                    <div
                        className='carousel__navigation__prev'
                        style={{ display: !isBeginning ? 'flex' : 'none' }}
                        onClick={goPrev}
                    >
                        <LazyLoadImage src={LeftArrow} alt='LeftArrow' style={{ marginRight: 5 }} />
                        {page}.
                    </div>

                    <div
                        className='carousel__navigation__next'
                        style={{ display: !isEnd ? 'flex' : 'none' }}
                        onClick={goNext}
                    >
                        {page + 2}.
                        <LazyLoadImage src={RightArrow} alt='RightArrow' style={{ marginLeft: 5 }} />
                    </div>
                </MediaQuery>

            </div>
        </div>
    )
}