import React from 'react';
import { Link } from "react-router-dom";
import MediaQuery from 'react-responsive';

export function Card(props) {
	return (
		<div className={`card ${props.className} ${props.isInteraction ? 'card__interaction__trigger' : ''}`} style={props.style} onTouchStart={function () { return null }}>
			<div className='card__image'>
				{props.image}
			</div>
			<div className='card__text'>
				{props.text}
			</div>

			<MediaQuery maxWidth={768}>
				<div style={{ display: props.isInteraction ? 'flex' : 'none' }}>
					<div className='card__interaction' style={{ display: props.isShowInteraction ? 'flex' : 'none' }}>
						{props.interaction}
					</div>
				</div>
			</MediaQuery>

		</div>
	);
}

export function CardButton(props) {
	return (
		<Link className={`card ${props.className} ${props.isInteraction ? 'card__interaction__trigger' : ''}`} style={props.style} to={props.to || ''} >
			<div className='card__image'>
				{props.image}
			</div>
			<div className='card__text'>
				{props.text}
			</div>

			<MediaQuery maxWidth={768}>
				<div style={{ display: props.isInteraction ? 'flex' : 'none' }}>
					<div className='card__interaction' >
						{props.interaction}
					</div>
				</div>
			</MediaQuery>

		</Link>
	);
}