import React from 'react';
import { Route, Switch } from "react-router-dom";
import Navbar from './components/Navbar';
import Home from './pages/Home';
import About from './pages/About';
import Finance from './pages/Finance';
import CarExchange from './pages/CarExchange';
import Refinance from './pages/Refinance';
import RedirectTo from './pages/RedirectTo';
import './assets/scss/App.scss'

const redirectToFinance = () => <RedirectTo to='/carrefinance' />
const redirectToRefinance = () => <RedirectTo to='/usedcarfinance' />
const redirectToHome = () => <RedirectTo to='/' />

class App extends React.Component {

	render() {
		return (
			<div>
				<Navbar />

				<Switch>
					<Route exact path="/" component={Home} />
					<Route exact path="/about" component={About} />
					<Route exact path="/usedcarfinance" component={Finance} />
					<Route exact path="/usedcarfinance/faq" render={(props) => <Finance {...props} currentTab={1} />} />
					<Route exact path="/carforcash" component={CarExchange} />
					<Route exact path="/carforcash/faq" render={(props) => <CarExchange {...props} currentTab={1} />} />
					<Route exact path="/carrefinance" component={Refinance} />
					<Route exact path="/carrefinance/faq" render={(props) => <Refinance {...props} currentTab={1} />} />
					<Route exact path="/refinance" component={redirectToFinance} />
					<Route exact path="/finance" component={redirectToRefinance} />.
					<Route component={redirectToHome} />
				</Switch>

			</div>
		);
	}
}

export default App;
