import React from 'react';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import MediaQuery from 'react-responsive';

export default function CustomExpansionPanel(props) {
	const [expanded, setExpanded] = React.useState(props.expand ? props.expand : false);

	React.useEffect(() => {
		if (!expanded && props.expand) 
		{
			setExpanded(props.expand);
		}
	}, [props.expand, expanded]);

	const handleClick = (i) => {
		if(props.callback){
			if(!expanded){
				props.callback(i);
			}else{
				props.callback('');
			}
		}
	}

	return (
		<ExpansionPanel
			id={props.id}
			expanded={expanded}
			onClick={handleClick.bind(this, props.url)}
			onChange={async () => {
				await setExpanded(!expanded);
			}}
			className='expansion__container'
		>
			<ExpansionPanelSummary
				className='expansion__summary'
				expandIcon={<FontAwesomeIcon icon={faChevronDown} className='expansion__arrow' />}
				aria-controls="panel1bh-content"
				id="panel1bh-header"
			>
				<MediaQuery minWidth={360}>
					<div className='large__text dark__blue' style={{ margin: 0, fontSize: 26 }}>{props.summary}</div>
				</MediaQuery>

				<MediaQuery maxWidth={359}>
					<div className='large__text dark__blue' style={{ margin: 0, fontSize: 22 }}>{props.summary}</div>
				</MediaQuery>
			</ExpansionPanelSummary>
			<ExpansionPanelDetails
				className='expansion__detail'
			>
				<div className='expansion__detail__text'>{props.detail}</div>
			</ExpansionPanelDetails>
		</ExpansionPanel>
	)
}