import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs, Tab, Address, SocialMedia, Map, LazyLoadBox } from '../components/Content';
import GetStarted from '../components/GetStarted';
import ExpansionPanel from '../components/ExpansionPanel';
import GetStartedImage1 from '../assets/img/content/get_start_6.png';
import GetStartedImage2 from '../assets/img/content/get_start_2.png';
import GetStartedImage3 from '../assets/img/content/get_start_3.png';
import GetStartedImage4 from '../assets/img/content/get_start_4.png';
import HeaderImage from '../assets/img/header/refinance.png';
import WarningImage from '../assets/img/warning_icon.svg';
import MediaQuery from 'react-responsive';
import { Helmet } from "react-helmet";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import LogoImage from '../assets/img/logo.png';
import { withRouter } from 'react-router-dom'
import axios from 'axios';

class Refinance extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			currentTab: 0,
			carrRefinanceFaqData: [],
			guarantorFaqData: [],
			isLoading: true
		};
	}

	scrollTo = (ready) => {	
		if(ready){
			let el = document.getElementById(this.props.history.location.hash.substr(1));
			if (el !== null){
				setTimeout(function(){
					el.scrollIntoView(true);
				}, 10)
			}
		}
	}

	pushRoute = async (url) => {
		await this.props.history.push("/carrefinance/faq#" + url)
	}

	setCurrentTab = async (data) => {
		await this.setState({ currentTab: data })

		switch (this.state.currentTab) {
			case 0:
				await this.props.history.push("/carrefinance");
				break;
			case 1:
				await this.props.history.push("/carrefinance/faq");
				break;
			default:
				await this.props.history.push("/carrefinance");
				break;
		}

		let tab = document.getElementById('tab');

		if (tab.getBoundingClientRect().top > 100){
			window.scrollTo(0, 0);
		}else{
			document.getElementById('tab__ref').scrollIntoView(true);
		}
	}

	async componentWillMount() {
		var carrRefinanceFaqLoading = true;
		var guarantorFaqLoading = true;
		await axios.get(process.env.REACT_APP_API_URL + '/api/carrefinanceFAQ', {
			headers: {
				'Content-Type': 'application/json',
			}
		}).then((response) => {
			this.setState({carrRefinanceFaqData : response.data});
			carrRefinanceFaqLoading = false;
			this.setState({isLoading: carrRefinanceFaqLoading || guarantorFaqLoading})
			this.scrollTo(!this.state.isLoading)
		})

		await axios.get(process.env.REACT_APP_API_URL + '/api/guarantorFaq', {
			headers: {
				'Content-Type': 'application/json',
			}
		}).then((response) => {
			this.setState({guarantorFaqData : response.data});
			guarantorFaqLoading = false;
			this.setState({isLoading: carrRefinanceFaqLoading || guarantorFaqLoading})
			this.scrollTo(!this.state.isLoading)
		})
	}

	componentDidMount() {
		window.scrollTo(0, 0)
		this.setState({ currentTab: this.props.currentTab ? this.props.currentTab : 0 })
	}

	render() {
		var carrRefinanceFaq = <LazyLoadBox/>;
		var guarantorFaq = <LazyLoadBox/>;
		if (!this.state.isLoading) {
			carrRefinanceFaq = [];
			guarantorFaq = [];

			for(let i = 0; i < this.state.carrRefinanceFaqData.length; i++){
				carrRefinanceFaq.push(
					<div key={i}>
						<div id={this.state.carrRefinanceFaqData[i].url} className='scroll__ref'></div>
						<ExpansionPanel
							url={this.state.carrRefinanceFaqData[i].url}
							callback={this.pushRoute}
							expand={this.props.history.location.hash.substr(1) === this.state.carrRefinanceFaqData[i].url}
							summary={<h3 className='text__20 text__thai' style={{textAlign: 'left'}}>{this.state.carrRefinanceFaqData[i].header}</h3>}
							detail={
								<p className='dark__blue text__faq__body' dangerouslySetInnerHTML={{__html: this.state.carrRefinanceFaqData[i].body}}/>
							}
						/>
					</div>
				)
			}

			for(let i = 0; i < this.state.guarantorFaqData.length; i++){
				guarantorFaq.push(
					<div key={i}>
						<div id={this.state.guarantorFaqData[i].url} className='scroll__ref'></div>
						<ExpansionPanel
							url={this.state.guarantorFaqData[i].url}
							callback={this.pushRoute}
							expand={this.props.history.location.hash.substr(1) === this.state.guarantorFaqData[i].url}
							summary={<h3 className='text__20 text__thai' style={{textAlign: 'left'}}>{this.state.guarantorFaqData[i].header}</h3>}
							detail={
								<p className='dark__blue text__faq__body' dangerouslySetInnerHTML={{__html: this.state.guarantorFaqData[i].body}}/>
							}
						/>
					</div>
				)
			}
		}

		const summaryText01 = <p>รีไฟแนนซ์รถคือ?</p>;

		const summaryText02 = <p>รีไฟแนนซ์รถได้หรือยัง?</p>;

		const detailText01 =
			<p className='dark__blue'>
				การย้ายที่ผ่อนรถ โดยปิดบัญชีที่ยังผ่อนอยู่<br />
				ด้วยเงินก้อนใหม่ที่กู้จากบริษัทไฟแนนซ์รถ<br />
				กับอัตราดอกเบี้ยใหม่ วงเงินใหม่<br />
				(ประเมินจากมูลค่ารถปัจจุบัน) จากนั้น<br />
				นำเงินที่เหลือจากการปิดบัญชีเก่าไปใช้จ่าย
			</p>;

		const detailText02 =
			<p className='dark__blue'>
				• ผ่อนมาแล้ว <font style={{ fontWeight: 'bold' }}>ประมาณครึ่งนึง</font>  ของยอดทั้งหมด<br />
				• รถใหม่กว่าปี 2004/2547<br />
				• <font style={{ fontWeight: 'bold' }}>ไม่ค้างค่างวด</font>ติดต่อกัน 2 เดือนล่าสุด
			</p>;

		let screenData = null;
		switch (this.state.currentTab) {
			case 0:
				screenData =
					<div>
						<ContactUs
							headerText={<p className='dark__blue'>รีไฟแนนซ์รถกับ CAR ATM</p>}
							warning={
								<span className='warning__text__large'>
									<LazyLoadImage src={WarningImage} alt='warning' style={{ height: '50%', marginRight: 16 }} />
									<p>
										• เฉพาะรถยนต์ หรือ รถกระบะ ใหม่กว่าปี 2004 (2547)<br/>
										• รถต้องไม่ค้างค่างวด<br/>
										• ควรผ่อนมาแล้วประมาณครึ่งหนึ่งของสัญญา
									</p>
								</span>
							}
						/>

						<MediaQuery maxWidth={768}>
							<ExpansionPanel
								summary={summaryText01}
								detail={detailText01}
							/>
							<ExpansionPanel
								summary={summaryText02}
								detail={detailText02}
							/>
						</MediaQuery>

						<MediaQuery minWidth={769}>
							<div className='duo__text__container'>
								<div className='left__text__container' style={{ alignItems: 'flex-end' }}>
									<div>
										<div className='large__text dark__blue'>
											{summaryText01}
										</div>
										<div className='medium__text dark__blue detail__text' style={{ marginTop: 30 }}>
											{detailText01}
										</div>
									</div>
								</div>
								<div className='right__text__container'>
									<div className='large__text dark__blue'>
										{summaryText02}
									</div>
									<div className='medium__text dark__blue detail__text' style={{ marginTop: 30 }}>
										{detailText02}
									</div>
								</div>
							</div>
						</MediaQuery>

						<GetStarted
							getStartedData={
								[{
									image: GetStartedImage1,
									imageAlt: 'คุณสมบัติไฟแนนซ์รถ',
									headerText:
										<p className='dark__blue'>1. เช็คก่อน<font className='blue'>รีไฟแนนซ์รถได้หรือยัง?</font></p>,
									headerTextMobile:
										<p className='dark__blue'>เช็คก่อน รีไฟแนนซ์รถได้หรือยัง?</p>,
									bodyText:
										<p className='dark__blue' style={{ textAlign: 'left' }}>
											• ผ่อนมาแล้ว <font style={{ fontWeight: 'bold' }}>ประมาณครึ่งนึง</font> ของยอดทั้งหมด<br></br>
											• รถ<font style={{ fontWeight: 'bold' }}>ใหม่กว่าปี 2004/2547</font><br></br>
											• <font style={{ fontWeight: 'bold' }}>ไม่ค้างค่างวด</font>ติดต่อกัน 2 เดือนล่าสุด
										</p>,
									bodyTextMobile:
										<p className='dark__blue' style={{ textAlign: 'left', paddingLeft: 30, paddingRight: 30 }}>
											• ผ่อนมาแล้ว <font style={{ fontWeight: 'bold' }}>ประมาณครึ่งนึง</font> ของยอดทั้งหมด<br></br>
											• รถ<font style={{ fontWeight: 'bold' }}>ใหม่กว่าปี 2004/2547</font><br></br>
											• <font style={{ fontWeight: 'bold' }}>ไม่ค้างค่างวด</font>ติดต่อกัน 2 เดือนล่าสุด
										</p>,
								}, {
									image: GetStartedImage2,
									imageAlt: 'ติดต่อบริษัทสินเชื่อ ไฟแนนซ์รถ',
									headerText:
										<p className='dark__blue'>2. <font className='blue'>ติดต่อบริษัทไฟแนนซ์รถ</font>เพื่อสรุปวงเงิน</p>,
									headerTextMobile:
										<p className='dark__blue'>ติดต่อบริษัทไฟแนนซ์รถเพื่อสรุปวงเงิน</p>,
									bodyText:
										<p className='dark__blue'>
											ติดต่อหาบริษัทไฟแนนซ์รถ<br></br>
											เพื่อประเมินวงเงิน และ ดอกเบี้ยในการจัดไฟแนนซ์รถ <br></br>
											ปัจจัยของวงเงินและดอกเบี้ยจะขึ้นกับ<br></br>
											<font className='text__bold'>รถของคุณ</font>, <font className='text__bold'>วงเงินที่ต้องการ</font>, <font className='text__bold'>รายได้ของผู้รีไฟแนนซ์รถ</font>
										</p>,
									bodyTextMobile:
										<p className='dark__blue'>
											ติดต่อหาบริษัทไฟแนนซ์รถ<br></br>
											เพื่อประเมินวงเงิน<br></br>
											และ ดอกเบี้ยในการจัดไฟแนนซ์รถ<br></br>
											ปัจจัยของวงเงินและดอกเบี้ยจะขึ้นกับ<br></br>
											1. <font className='text__bold'>รถของคุณ</font><br></br>
											2. <font className='text__bold'>วงเงินที่ต้องการ</font><br></br>
											3. <font className='text__bold'>รายได้ของผู้รีไฟแนนซ์รถ</font>
										</p>
								}, {
									image: GetStartedImage3,
									imageAlt: 'จัดเตรียมเอกสาร',
									headerText:
										<p className='dark__blue'>3. จัดเตรียม<font className='blue'>เอกสาร</font></p>,
									headerTextMobile:
										<p className='dark__blue'>จัดเตรียมเอกสาร</p>,
									bodyText:
										<p className='dark__blue'>
											จัดเตรียมเอกสารต่างๆให้พร้อม<br></br>
											เตรียมยื่นให้บริษัทไฟแนนซ์รถ เช่น<br></br>
											<font className='text__bold'>สมุดทะเบียนรถ,</font> <font className='text__bold'>บัตรประชาชน,</font> <font className='text__bold'>ทะเบียนบ้าน,</font><br></br>
											<font className='text__bold'>เอกสารแสดงรายได้ย้อนหลัง</font> เป็นต้น<br></br>
											ยิ่งพร้อมเร็ว ยิ่งผ่านเร็วครับ
										</p>,
									bodyTextMobile:
										<p className='dark__blue'>
											จัดเตรียมเอกสารต่างๆให้พร้อม<br></br>
											เตรียมยื่นให้บริษัทไฟแนนซ์รถ เช่น<br></br>
											<font className='text__bold'>สมุดทะเบียนรถ,</font><br></br>
											<font className='text__bold'>บัตรประชาชน,</font><br></br>
											<font className='text__bold'>ทะเบียนบ้าน,</font><br></br>
											<font className='text__bold'>เอกสารแสดงรายได้ย้อนหลัง</font> เป็นต้น<br></br>
											ยิ่งพร้อมเร็ว ยิ่งผ่านเร็วครับ
										</p>
								}, {
									image: GetStartedImage4,
									imageAlt: 'เซ็นสัญญา',
									headerText:
										<p className='dark__blue'>4. <font className='blue'>นัดเซ็นสัญญา </font>และ <font className='blue'>โอนเล่มทะเบียนรถ</font></p>,
									headerTextMobile:
										<p className='dark__blue'>นัดเซ็นสัญญา และ โอนเล่มทะเบียนรถ</p>,
									bodyText:
										<p className='dark__blue'>
											หลังจากเอกสารพร้อม <font className='text__bold'>บริษัทไฟแนนซ์นัดเซ็นสัญญา</font><br></br>
											นัดโอนเล่มทะเบียนรถ และรับเงินไปใช้ได้ทันที<br></br>
											(ใช้เวลา 2-3 วัน)
										</p>,
									bodyTextMobile:
										<p className='dark__blue'>
											หลังจากเอกสารพร้อม<br></br>
											<font className='text__bold'>บริษัทไฟแนนซ์นัดเซ็นสัญญา</font><br></br>
											นัดโอนเล่มทะเบียนรถ<br></br>
											และรับเงินไปใช้ได้ทันที<br></br>
											(ใช้เวลา 2-3 วัน)
										</p>
								}]
							}

						/>
						<WhyChooseUs />

						<AboutUs
							slot={
								<MediaQuery maxWidth={1024}>
									<ContactUs
										headerText={<p className='dark__blue'>รีไฟแนนซ์รถกับ CAR ATM</p>}
										warning={
											<span className='warning__text__large'>
												<LazyLoadImage src={WarningImage} alt='warning' style={{ height: '50%', marginRight: 16 }} />
												<p style={{ display: 'flex', flexDirection: 'column' }}>
													• เฉพาะรถยนต์ หรือ รถกระบะ ใหม่กว่าปี 2004 (2547)<br/>
													• รถต้องไม่ค้างค่างวด<br/>
													• ควรผ่อนมาแล้วประมาณครึ่งหนึ่งของสัญญา
												</p>
											</span>
										}
									/>
								</MediaQuery>
							}
						/>
					</div>
				break;

			case 1:
				screenData =
					<div>
						<div className='content' style={{ backgroundColor: '#F2F2F2', maxWidth: 'none' }}>
							<div style={{ display: 'flex', flexDirection: 'column', maxWidth: 728, width: '100%' }}>
								<MediaQuery minWidth={728}><div style={{ height: 35 }}></div></MediaQuery>
								{carrRefinanceFaq}

								<MediaQuery minWidth={728}><div style={{ height: 35 }}></div></MediaQuery>
								<div style={{ alignSelf: 'flex-start', marginLeft: 30, marginBottom: 16 }}>
									<p className="larger__text header__text dark__blue" style={{ marginBottom: 0 }}>
										เกี่ยวกับการค้ำประกัน
									</p>
									<p className="medium__text dark__blue" style={{ textAlign: 'start' }}>
										(กรณีต้องมีคนค้ำ)
									</p>
								</div>

								{guarantorFaq}
							
							</div>
						</div>
						<div id='contact' className='scroll__ref'></div>
						<ContactUs
							headerText={
								<p className='larger__text dark__blue' style={{ flexDirection: 'row', marginTop: -10 }}>
									มีคำถามอื่นๆ <font style={{ color: '#EB5757' }}>ติดต่อเราได้ทันที</font>
								</p>}
							promotionText={false}
							subHeaderText={
								<p className='dark__blue' style={{ marginTop: 5 }}>จัดไฟแนนซ์ตอนนี้ ลดดอกเบี้ยพิเศษ!</p>
							}
							warning={<div></div>}
						/>
						<div className='content'>
							<LazyLoadImage src={LogoImage} alt='Logo' className='about__logo__image' />
							<Address />
							<SocialMedia />
							<Map />
						</div>
					</div>
				break;
			default:
				break;
		}

		return (
			<div className='container'>
				<Helmet>
					<meta charSet="utf-8" />
					<title>รีไฟแนนซ์รถที่ผ่อนอยู่ | บริการมากว่า 30 ปี - CAR ATM</title>
					<meta name="description" content="บริการรีไฟแนนซ์รถยังผ่อนไม่หมด ปิดบัญชีผ่อนเก่า รับเงินกู้ใหม่ทันที ให้วงเงินสูง ดอกเบี้ยต่ำ บริการรวดเร็ว พนักงานรับสายมีประสบการณ์ ปิดดีลได้ทันที" />
					<link rel="canonical" href="https://caratmthailand.com/carrefinance" />
					<meta name="robots" content="index, follow" />
					<meta property="og:type" content="article" />
					<meta property="og:title" content="รีไฟแนนซ์รถที่ผ่อนอยู่ | บริการมากว่า 30 ปี - CAR ATM" />
					<meta property="og:description" content="บริการรีไฟแนนซ์รถยังผ่อนไม่หมด ปิดบัญชีผ่อนเก่า รับเงินกู้ใหม่ทันที ให้วงเงินสูง ดอกเบี้ยต่ำ บริการรวดเร็ว พนักงานรับสายมีประสบการณ์ ปิดดีลได้ทันที" />
					<meta property="og:url" content="https://caratmthailand.com/carrefinance" />
					<meta property="og:site_name" content="caratmthailand.com" />
				</Helmet>

				<Header
					slot={
						<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
							<h1 className='largest__text blue'>
								รีไฟแนนซ์รถ
							</h1>

							<LazyLoadImage src={HeaderImage} alt='Header' className='header__image' />
						</div>
					}
				/>
				<Tab tabs={['รายละเอียด', 'คำถามที่พบบ่อย']} activeTab={this.state.currentTab} callback={this.setCurrentTab} id='tab'/>
				<div>
					<div id='tab__ref' className='scroll__ref'></div>
				</div>
				{screenData}
			</div>
		);
	}
}

export default withRouter(Refinance)